/*--------------------------*/
@-moz-document url-prefix() {
  select, select:-moz-focusring, select::-moz-focus-inner {
      color: transparent !important;
      text-shadow: 0 0 0 #000 !important;
      background-image: none !important;
      border:0;
  }
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  width: 75%;
  max-width: 1000px;
  margin: 0 auto;
}
.wrapper {padding: 15px 25px;}
.gold {color: #d49b41;}
/*--------------------------*/
#up {
  display: none;
  padding: 5px;
  background-color: rgba(0,0,0,0.65);
  position: fixed;
  bottom: 25px;
  right: 25px;
  z-index: 9999;
  color: #f7f5f3;
  cursor: pointer;
}
#up i {font-size: 35px;}
/*--------------------------*/
.dark {
	position: fixed;
	height: 100vh;
	width: 100%;
	background-color: rgba(0,0,0,0.35);
	display: inline-block;
	left: 0;
	top: 0;
}
/*--------------------------*/
.xbutton {
	border: 1px solid tan !important;
	margin-right: 8px;
	padding: 7px 13px;
	cursor: pointer;
  border-radius: 7px;
  transition: .3s;
}
.xbutton:hover {
  background-color: #dddada;
}
/*--------------------------*/
.flex-bet {
  width: 100%;
  margin: 0 auto; 
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.flex-in {
  width: 100%;
  margin: 0 auto; 
  display: inline-flex;
  justify-content: flex-start;
}
.flex-st {
  width: 100%;
  margin: 0 auto; 
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.flex-cent {
  width: 100%;
  margin: 0 auto; 
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.flex-round {
  width: 100%;
  margin: 0 auto; 
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.flex-end {
  width: 100%;
  margin: 0 auto; 
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}
/*--------------------------*/
form input {box-sizing: border-box !important;}
form input, form textarea {
  background-color: #f7f5f3 !important;
  border: 1px solid #c8a46d !important;
  margin: 5px 0;
  border-radius: unset;
  padding-left: 10px !important;
}
textarea {
  height: unset;
  /* width: 100% !important; */
  padding-top: 10px !important;
}
/*--------------------------*/
#editor-wrapper {
  border: 1px solid tan;
  min-height: 250px;
}
#editor {
  padding: 5px;
  outline: none;
}
#tools {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  border-bottom: 1px solid tan;
  background-color: #f0efef;
}
#tools * {
	color: #333;
	-webkit-user-select: none !important;
	-moz-user-select: none !important;
	-ms-user-select: none !important;
	user-select: none !important;
}
.tools-group {display: flex;}
.tools-group i {
  margin: 5px 0;
  cursor: pointer;
  border-right: 1px solid #888;
  padding: 0 8px;
}
.tools-group svg {
  margin: 5px 0;
  cursor: pointer;
  border-right: 1px solid #888;
  padding: 0 8px;
}
.tools-group h5 {
  margin: 5px 0;
  cursor: pointer;
  border-right: 1px solid #888;
  padding: 0 8px;
}
.tools-group > button {
  margin: 5px 0;
  cursor: pointer;
  border-right: 1px solid #888;
  padding: 0 8px;
  border-left: none;
  border-top: none;
  border-bottom: none;
  background-color: inherit;
}
.tools-group button:first-child {border-left: 1px solid #888;}
.tools-group button:last-child {border-right: none;}
.br {border-right: 1px solid #888 !important;}
.bl {border-left: 1px solid #888 !important;}
.tools-group h5:last-child {border-right: none;}
.tools-group:not(:first-child) i:first-child {border-left: 1px solid #888;}
.tools-group:not(:last-child) i:last-child {border-right: none;}
